/**
 * LoadingPage Component
 *
 * A beautiful loading page component that displays during data fetching,
 * page transitions, or other async operations. Follows the project's design system
 * with consistent branding and modern UI patterns.
 *
 * Features:
 * - Animated loading spinner
 * - PMS brand integration
 * - Multiple loading states
 * - Responsive design
 * - Accessibility support
 * - Customizable messages
 *
 * @component
 * @example
 * ```tsx
 * // Basic loading page
 * <LoadingPage />
 *
 * // Custom message
 * <LoadingPage message="Fetching your data..." />
 *
 * // Overlay mode for modals
 * <LoadingPage overlay={true} message="Saving changes..." />
 *
 * // Small spinner for buttons
 * <LoadingSpinner size="sm" />
 *
 * // Loading dots animation
 * <LoadingDots />
 *
 * // Skeleton for content
 * <LoadingSkeleton className="h-4 w-32" />
 * ```
 */

import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Loader2 } from "lucide-react";

interface LoadingPageProps {
  /** Custom loading message */
  message?: string;
  /** Whether to show as overlay instead of full page */
  overlay?: boolean;
  /** Size of the loading spinner */
  size?: "sm" | "md" | "lg";
  /** Whether to show the PMS logo */
  showLogo?: boolean;
  /** Additional CSS classes */
  className?: string;
}

/**
 * LoadingPage - Beautiful loading component with branding
 *
 * Displays a loading state with animated spinner, PMS branding, and customizable
 * messaging. Can be used as a full page or overlay component.
 */
const LoadingPage = ({
  message = "Loading...",
  overlay = false,
  size = "lg",
  showLogo = true,
  className = "",
}: LoadingPageProps) => {
  const spinnerSizes = {
    sm: "h-4 w-4",
    md: "h-8 w-8",
    lg: "h-12 w-12",
  };

  const containerClasses = overlay
    ? "fixed inset-0 z-50 flex items-center justify-center bg-background/80 backdrop-blur-sm"
    : "flex items-center justify-center p-4";

  return (
    <div className={`${containerClasses} ${className}`}>
      <Card className="w-full max-w-2xl">
        <CardHeader className="text-center space-y-4">
          {/* PMS Logo */}
          {showLogo && (
            <div className="flex items-center justify-center">
              <div className="h-16 w-16 rounded-lg bg-primary flex items-center justify-center">
                <span className="text-primary-foreground font-bold text-xl">
                  PMS
                </span>
              </div>
            </div>
          )}

          {/* Loading Spinner */}
          <div className="flex justify-center">
            <Loader2
              className={`${spinnerSizes[size]} animate-spin text-primary`}
            />
          </div>

          {/* Loading Message */}
          <div className="space-y-2">
            <CardTitle className="text-xl font-semibold">{message}</CardTitle>
            <CardDescription>
              Please wait while we prepare your content
            </CardDescription>
          </div>
        </CardHeader>

        <CardContent>
          {/* Loading Progress Indicator */}
          <div className="space-y-3">
            <div className="w-full bg-muted rounded-full h-2">
              <div className="bg-primary h-2 rounded-full animate-pulse w-3/4"></div>
            </div>
            <p className="text-xs text-center text-muted-foreground">
              This may take a few moments...
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

/**
 * LoadingSpinner - Minimal loading spinner component
 *
 * A simple, reusable loading spinner for inline loading states.
 *
 * @example
 * ```tsx
 * <LoadingSpinner size="sm" />
 * <LoadingSpinner size="md" className="text-blue-500" />
 * ```
 */
export const LoadingSpinner = ({
  size = "md",
  className = "",
}: {
  size?: "sm" | "md" | "lg";
  className?: string;
}) => {
  const spinnerSizes = {
    sm: "h-4 w-4",
    md: "h-6 w-6",
    lg: "h-8 w-8",
  };

  return (
    <Loader2 className={`${spinnerSizes[size]} animate-spin ${className}`} />
  );
};

/**
 * LoadingDots - Animated dots loading indicator
 *
 * Alternative loading animation using animated dots.
 *
 * @example
 * ```tsx
 * <LoadingDots />
 * ```
 */
export const LoadingDots = ({ className = "" }: { className?: string }) => {
  return (
    <div className={`flex space-x-1 ${className}`}>
      <div className="w-2 h-2 bg-primary rounded-full animate-bounce"></div>
      <div
        className="w-2 h-2 bg-primary rounded-full animate-bounce"
        style={{ animationDelay: "0.1s" }}
      ></div>
      <div
        className="w-2 h-2 bg-primary rounded-full animate-bounce"
        style={{ animationDelay: "0.2s" }}
      ></div>
    </div>
  );
};

/**
 * LoadingSkeleton - Skeleton loading for content
 *
 * Displays placeholder content while data is loading.
 *
 * @example
 * ```tsx
 * <LoadingSkeleton className="h-4 w-32" />
 * <LoadingSkeleton className="h-8 w-full" />
 * ```
 */
export const LoadingSkeleton = ({ className = "" }: { className?: string }) => {
  return <div className={`animate-pulse bg-muted rounded ${className}`} />;
};

export default LoadingPage;
